html,
body {
  margin: 0;
  padding: 0;
}
* {
  box-sizing: border-box;
  color: var(--black);
}

.calculator-wrapper {
  width: 320px;
  margin: 50px auto 0 auto;
  padding: 30px 0 50px 0;
  border-radius: 15px;
  background-color: var(--white);
  box-shadow: rgba(9, 30, 66, 0.25) 0px 4px 8px -2px,
    rgba(9, 30, 66, 0.08) 0px 0px 0px 1px;
  /* outline: 1px solid red; */
}

.display-wrapper {
  width: calc(320px - (16px * 2));
  height: 140px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-bottom: 20px;
  /* outline: 1px solid red; */
}
.display-title {
  height: 20px;
  margin-bottom: 40px;
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  /* border: 1px solid black; */
}
.display-title:after {
  content: '';
  width: 48px;
  display: block;
  margin: 2px auto 0 auto;
  border-bottom: 3px solid var(--light-red);
}
.display-small {
  height: 24px;
  text-align: right;
  letter-spacing: 2px;
  font-size: 16px;
  line-height: 24px;
  padding: 0 40px;
  overflow: hidden;
  /* border: 1px solid black; */
}
.display-main {
  height: 54px;
  text-align: right;
  font-size: 54px;
  line-height: 54px;
  padding: 0 40px;
  overflow: hidden;
  /* border: 1px solid black; */
}

.buttons-wrapper {
  width: calc(64px * 4);
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  /* outline: 1px solid red; */
}
